/* TAILWIND IMPORT  ---------------------------*/
@tailwind base;
@tailwind components;
@tailwind utilities;


/* SASS IMPORTS  ---------------------------*/
@import 'config';
@import 'theme';
@import 'fonts';
@import 'base';


/* ANIMATIONS  ---------------------------*/
@keyframes twist {
  0% {
    transform: translate(-50%, -50%) rotate(-8deg);
  }
  50% {
    transform: translate(-50%, -50%) rotate(8deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(-8deg);
  }
}

@keyframes spin {
  0% {
    transform: translate(-50%, -50%) rotate(0);
  }
  100% {
    transform: translate(-50%, -50%) rotate(359deg);
  }
}


/* NAV ELEMENTS  ---------------------------*/
.h4c-logo {
	width: 10.9375vw;
	left: 3.90625vw;
	top: 3.125vw;
}
.btyb-juliet {
	width: 10.9375vw;
	left: 3.90625vw;
	bottom:3.125vw;
}
.hamburger-menu,
.icon-close-menu {
	top: 3.125vw;
	right: 3.90625vw;
}
.hamburger-menu {
	width: 4.296875vw;
	.hover {
		transition: all 0.2s ease-in-out;
	}

	&:hover .hover {
		transform: translatex(130px);
	}
}
.icon-close-menu {
	width: 3.4375vw;
}
.submit-cta {	
	font-family: $sans-serif;
	background-color: $lavender;
	color: $lime;
	padding: 0.78125vw 1.015625vw;
	font-size: 1.5625vw;
}
.submit-cta,
.contact-cta {
	bottom: 3.125vw;
	right: 3.90625vw;
}
.contact-cta {
	h2, a {
		color: $lavender;
	}
	h2 {
		font-size: 1.40625vw;
	}
	a {
		font-size: 1.25vw;
		background-image: linear-gradient(to right, $plum, $plum 50%, $lavender 50%);
	}
}


#app {
	transition: all 0.6s ease-in-out;
}

/* SECTIONS  ---------------------------*/
.card,
.slate {
	padding: 4.9vw;
	@include atMedia($min, $xlDesktop) {
		padding: 98px;
	}
}

// Slates
.slate {
	color: $red;

	p {
		font-size: 1.25vw;
		@include atMedia($min, $xlDesktop) {
			font-size: 1.5625rem;
		}
	}

	.heading {
		transition: all 0.3s ease-in-out;

		&:before, &:after {
			content: "";
			position: absolute;
			top: 50%;
			left: 50%;
			transition: all 0.3s ease-in-out;
		}

		//Fat border
		&:before {
			width: 54vw;
			height: 32vw;
			max-width: 1080px;
			max-height: 640px;
		    z-index: -1;
		    transform: translate(-50%, -50%) rotate(-8deg);
			border: 1.5625vw solid $lavender;
			@include atMedia($min, $xlDesktop) {
				border: 31.25px solid $lavender;
			}
			animation: 20s ease-in-out 1s infinite twist;
		}

		//Thin border
		&:after {
			width: 52.5vw;
			height: 30vw;
			max-width: 1000px;
			max-height: 600px;
		    z-index: -2;
		    transform: translate(-50%, -50%);
		    border: 0.15vw solid $slime;
		    @include atMedia($min, $xlDesktop) {
				border: 3px solid $slime;
			}
		}
	}

	//Main hero
	&:nth-of-type(1) {
		.background {
			z-index: -2;

			[class*="rect"] {
			    border: 0.15vw solid $slime;
			    @include atMedia($min, $xlDesktop) {
					border: 3px solid $slime;
				}
			}
			.rect1 {
				width: 21.5vw;
			    height: 21.5vw;
			    max-width: 428px;
			    max-height: 428px;
			    top: 18.5%;
			    left: -11.5%;
			    @include spin("rect1", 0deg, 300s, 8s, null);
			}
			.rect2 {
			    width: 32.5vw;
			    height: 32.5vw;
				max-width: 650px;
				max-height: 650px;
			    top: -50.5%;
			    left: 26.5%;
			    transform: rotate(-7deg);
			    @include spin("rect2", -7deg, 200s, 0s, "reverse");
			}
			.rect3 {
				width: 28vw;
    			height: 28vw;
				max-width: 560px;
				max-height: 560px;
				bottom: 18.5%;
				right: -18.5%;
				transform: rotate(15deg);
				@include spin("rect3", 15deg, 175s, 2s, "reverse");
			}
			.rect4 {
				width: 15.5vw;
			    height: 30.5vh;
			    max-width: 310px;
				max-height: 407px;
			    left: 56%;
    			top: 87%;
			    transform: rotate(-32deg);
			    @include spin("rect4", -32deg, 200s, 3s, null);
			}
		}
	}

	//Final screen
	&:last-child {
		.container button {
			font-family: $sans-serif;
			font-size: 1.8vw;
			@include atMedia($min, $xlDesktop) {
				font-size: 2.25rem;
			}
			// color: $red;
			background-image: linear-gradient(to right, $plum, $plum 50%, $red 50%);
			// transition: color 0.3s ease-in-out;

			&:hover {
				color: $plum;
			}
		}

		.heading.pause {
			transform: scale(0.99);
			&:before {
				animation-play-state: paused;
				filter: blur(2px);
				border-color: $red;
			}
			&:after {
				filter: blur(2px);
			}
		}
	}
}

//Cards
.card {
	.heading p {
		font-family: $serif;
		font-weight: normal;
		text-transform: none;
		margin: 0.5rem 0;
		font-size: 1.953125vw;
		@include atMedia($min, $xlDesktop) {
			font-size: 2.4375rem;
		}
	}

	&:nth-of-type(2n) {
		transform: translatey(100%);
	}
	&:nth-of-type(2n+1) {
		left: 50%;
		transform: translatey(-100%);
	}
}
.card--image {
	padding: 0;
}
.copy p {
	font-size: 1rem;
	@include atMedia(null, $mdDesktop, $pre-xlDesktop) {
		font-size: 1.125vw;
	}
	@include atMedia($min, $xlDesktop) {
		font-size: 1.5625rem; //25px
	}
}


/* MENU  ---------------------------*/

#menu {
	background-color: $lavender;
	overflow: hidden;
	transform: translatex(0);
	transition: 
	transform 1.2s cubic-bezier(0.25, 1, 0.5, 1), 
	background-color 1.2s cubic-bezier(0.25, 1, 0.5, 1);

	.container {
		padding: 0 20px;
	}
	
	.container, 
	.icon-close-menu,
	.contact-cta  {
		transition: transform 1.2s cubic-bezier(0.25, 1, 0.5, 1);
		position: fixed;
		transform: translatex(0);
	}
    
    &.hidden {
    	display: flex;
    	background-color: $plum;
    	transform: translatex(100vw);
    	.container,
    	.icon-close-menu,
    	.contact-cta {
			transform: translatex(-100vw);
		}
    }
	
	a {
		color: $plum;
		font-family: $sans-serif;
		font-size: 3.359375vw;
		line-height: 1.72;
		background-image: linear-gradient(to right, $lime, $lime 50%, $plum 50%);
	}
	
	.contact-cta {
		h2 {
			color: $plum;
		}
		a {
			font-size: 1.25vw;
			line-height: 1.5;
			font-family: $serif;
		}
	}

	li li a {
		font-family: $serif;
		font-size: 1.953125vw;
	}

	
}
.collapse {
	overflow: hidden;
	max-height: 0;
	transition: max-height 0.5s ease-in-out;
	&.active {
		max-height: 50vh;
	}
}